<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process110"></a>

<p><br></p>
<p><span class="rvts17">抵抗迹象（</span><span class="rvts50">Signs of Resistance</span><span class="rvts17">）</span><span class="rvts50">[</span><span class="rvts17">支线</span><span class="rvts50">]</span></p>
<p class="rvps2"><span class="rvts41">任务坐标：</span>
<br><span class="rvts20">X:203,Y:206</span><span class="rvts21">，地点：欢乐堡城门</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">目标：</span>
<br><span class="rvts21">找到寻觅者的避难所</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">任务详情：</span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1010.jpg" display="block"><span class="rvts20"></span>
<br><span class="rvts21">在进入欢乐堡城门处时，就遭遇了一场杀鸡儆猴的君臣大戏。这出剧是触发该任务的首要条件。</span>
<br><span class="rvts21">被处死的蜥蜴叫阿图萨（</span><span class="rvts20">Atusa</span><span class="rvts21">），之后在欢乐堡中进行探索，会遇到很多关于反抗迹象的信息。</span>
<br><span class="rvts20"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1011.jpg" display="block"><span class="rvts20"></span>
<br><span class="rvts21">在完成了维泽磨的灵柩或被囚禁的精灵后，会在探险路途中遇到很多与该任务有关的</span><span class="rvts20">NPC</span><span class="rvts21">。</span>
<br><span class="rvts21">最终该任务指向阿玛蒂亚避难所（</span><span class="rvts20">Sanctuary of Amadia</span><span class="rvts21">）里的一支秘密帮助源力者逃窜的支援队伍。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return { clickcount : 1  };
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    },
    timer(){
      if (this.clickcount < 3) {
        this.popScroll.refresh()
        this.clickcount = this.clickcount + 1
      }
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
    setInterval(this.timer, 2000);
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



